  --color-gray-500: #6b7280;
  --color-gray-600: #4b5563;
  --color-gray-700: #374151;
  --color-gray-800: #1f2937;
  --color-gray-900: #111827;
  --color-gray-950: #030712;
  
  /* ===== Cores de Fundo ===== */
  --color-background: #ffffff;
  --color-muted: var(--color-gray-50);
  --color-card: #ffffff;
  --color-popover: #ffffff;
  
  /* ===== Cores de Texto ===== */
  --color-foreground: var(--color-gray-900);
  --color-muted-foreground: var(--color-gray-500);
  --color-card-foreground: var(--color-gray-900);
  --color-popover-foreground: var(--color-gray-900);
  
  /* ===== Cores de UI ===== */
  --color-border: var(--color-gray-200);
  --color-input: var(--color-gray-200);
  
  /* Cores de Botões e Ações */
  --color-primary: var(--color-orange-600);
  --color-primary-foreground: #ffffff;
  
  --color-secondary: var(--color-gray-100);
  --color-secondary-foreground: var(--color-gray-900);
  
  --color-accent: var(--color-info-600);
  --color-accent-foreground: #ffffff;
  
  --color-destructive: var(--color-error-600);
  --color-destructive-foreground: #ffffff;
  
  --color-success: var(--color-success-600);
  --color-success-foreground: #ffffff;
  
  --color-warning: var(--color-warning-600);
  --color-warning-foreground: #ffffff;
  
  --color-ring: var(--color-orange-500);
  
  /* ===== Opacidades ===== */
  --opacity-10: 0.1;
  --opacity-20: 0.2;
  --opacity-30: 0.3;
  --opacity-40: 0.4;
  --opacity-50: 0.5;
  --opacity-60: 0.6;
  --opacity-70: 0.7;
  --opacity-80: 0.8;
  --opacity-90: 0.9;
  
  /* Border radius */
  --radius: 0.5rem;
