/* Apply the font to the body */
body {
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  background-color: var(--color-background);
  color: var(--color-foreground);
}

/* ===== Tema Escuro ===== */
@media (prefers-color-scheme: dark) {
  :root {
    /* Cores de Fundo */
    --color-background: var(--color-gray-950);
    --color-muted: var(--color-gray-900);
    --color-card: var(--color-gray-900);
    --color-popover: var(--color-gray-900);
    
    /* Cores de Texto */
    --color-foreground: var(--color-gray-100);
    --color-muted-foreground: var(--color-gray-400);
    --color-card-foreground: var(--color-gray-100);
    --color-popover-foreground: var(--color-gray-100);
    
    /* Cores de UI */
    --color-border: var(--color-gray-800);
    --color-input: var(--color-gray-800);
    
    /* Cores de Botões e Ações */
    --color-primary: var(--color-orange-500);
    --color-primary-foreground: var(--color-gray-950);
    
    --color-secondary: var(--color-gray-800);
    --color-secondary-foreground: var(--color-gray-100);
    
    --color-accent: var(--color-info-500);
    --color-accent-foreground: var(--color-gray-50);
    
    --color-destructive: var(--color-error-500);
    --color-destructive-foreground: var(--color-gray-50);
    
    --color-success: var(--color-success-500);
    --color-success-foreground: var(--color-gray-50);
    
    --color-warning: var(--color-warning-500);
    --color-warning-foreground: var(--color-gray-50);
    
    --color-ring: var(--color-orange-500);
  }
}

/* ===== Estilos Base ===== */
* {
  border-color: var(--color-border);
  transition: background-color 0.2s, border-color 0.2s, color 0.2s;
}

body {
  font-feature-settings: "rlig" 1, "calt" 1;
  
  /* Suave transição de cores para temas */
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* ===== Utilitários de Cor ===== */
/* Cores de fundo */
.bg-success { background-color: var(--color-success); }
.bg-info { background-color: var(--color-info); }
.bg-warning { background-color: var(--color-warning); }
.bg-error { background-color: var(--color-error); }

/* Cores de texto */
.text-success { color: var(--color-success-600); }
.text-info { color: var(--color-info-600); }
.text-warning { color: var(--color-warning-600); }
.text-error { color: var(--color-error-600); }

/* Versões escuras para tema escuro */
.dark .text-success { color: var(--color-success-400); }
.dark .text-info { color: var(--color-info-400); }
.dark .text-warning { color: var(--color-warning-400); }
.dark .text-error { color: var(--color-error-400); }

/* Opacidades */
.opacity-10 { opacity: var(--opacity-10); }
.opacity-20 { opacity: var(--opacity-20); }
.opacity-30 { opacity: var(--opacity-30); }
.opacity-40 { opacity: var(--opacity-40); }
.opacity-50 { opacity: var(--opacity-50); }
.opacity-60 { opacity: var(--opacity-60); }
.opacity-70 { opacity: var(--opacity-70); }
.opacity-80 { opacity: var(--opacity-80); }
.opacity-90 { opacity: var(--opacity-90); }

/* Gradientes */
.bg-gradient-primary {
  background: linear-gradient(135deg, var(--color-orange-500), var(--color-orange-700));
}

.bg-gradient-success {
  background: linear-gradient(135deg, var(--color-success-500), var(--color-success-700));
}

.bg-gradient-info {
  background: linear-gradient(135deg, var(--color-info-500), var(--color-info-700));
}

/* Sombras */
.shadow-xs {
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.05);
}

.shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.shadow {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

.shadow-md {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.shadow-xl {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

/* Transições */
.transition-colors {
  transition-property: background-color, border-color, color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

